<template>
  <div class="comp vip_card" :style="vipCardBg">
    <div class="vip_card_inner">
      <span class="vip_card_title_level"> {{ $t('UI_VIP_Vip.lv') }}{{ levelText }} </span>
      <!-- 已过等级显示 -->
      <span class="vip_card_title_level_cross">{{ levelTipText }}</span>
      <!-- 进度条 -->
      <vip-process
        :keep-level="isKeepLevel"
        :user-vip="user.vip_id"
        :item="item"
        :recharge-item="getRecharge"
        :is-kilobit="isKilobit"
      />
    </div>
    <!-- 加锁  保级状态 && 小于当前等级 -->
    <span v-if="isKeepLevel && item.id <= user.vip_id" class="vip_placeholder_lock">
      <icon-font name="icon-icon_VIP_lock" :size="iconSize"></icon-font>
    </span>
    <!-- 勋章 -->
    <div class="vip_card_medal" :class="{ highest: highest }">
      <img :src="medalImg" alt="" />
    </div>
    <!-- 背景角图 -->
    <!-- <div class="vip_horn"></div> -->
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import { getCacheIcon } from '@/assets/imgCache.js'
import VipProcess from './vip-process.vue'
import _common from '@style/views/recompensas.modules.scss'

const VIP_MEDAL = {
  1: 'icon_v1',
  2: 'icon_v2',
  3: 'icon_v3',
  4: 'icon_v4'
}
export default {
  name: 'VipCard',
  components: {
    VipProcess
  },
  props: {
    // vip 列表
    vipList: {
      type: Array,
      default() {
        return []
      }
    },
    // 单个 vip 数据
    item: {
      type: Object,
      default: () => {}
    },
    // 是否是最高等级
    highest: {
      type: Boolean,
      default: false
    },
    // vip 总等级
    totalGrade: {
      type: [Number, String],
      default: 0
    },
    // 是否是当前等级
    isActive: {
      type: Boolean,
      default: false
    },
    // 当前切换到的索引
    index: {
      type: [Number, String],
      default: 0
    },
    // vip千分位开关
    isKilobit: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {}
  },
  computed: {
    ...mapState('layout', ['containerBox']),
    ...mapState('base', ['isApp', 'user']),
    ...mapGetters('base', ['getVipCurrentLevel']),
    // 等级显示
    levelText() {
      return this.getVipCurrentLevel(this.item.id)
    },
    // 已过等级显示
    levelTipText() {
      const itemID = this.item.id
      const vipID = this.user.vip_id
      if (this.highest) {
        return this.$i18n.t('UI_VIP_highest')
      }
      let key = 'UI_VIP_Not_Reached'
      // 已过等级显示
      if (itemID < vipID) {
        key = 'UI_VIP_Grade_Passed'
      } else if (itemID == vipID) {
        key = 'UI_VIP_Current_Livel'
      }
      return this.$i18n.t(key)
    },
    // show_vip_list 里进度条下个等级的进度条，所以显示的应该是下一级的数据【+ 1】比如当前等级4，应该获取等级5的
    getRecharge() {
      const { id = 0 } = this.item
      const level = id + 1
      const item = this.vipList.find(f => f.id == level) || null
      return item
    },
    /**
     * 是否正在保级中
     * is_keep_level 0保级中 1未保级
     */
    isKeepLevel() {
      const { is_keep_level = 0 } = this.user
      return is_keep_level != 1
    },
    // 勋章显示
    medalImg() {
      // vip 总数 - 最高等级1个，其余平分 -- 显示图标
      const vip_l = this.totalGrade - 1
      // 区分等级模块
      const levelModel = [parseInt((vip_l / 3) * 1), parseInt((vip_l / 3) * 2), vip_l]
      const { id = 0 } = this.item
      let i = 1

      if (id <= levelModel[0]) {
        i = 1
      } else if (id <= levelModel[1]) {
        i = 2
      } else if (id <= levelModel[2]) {
        i = 3
      } else {
        i = 4
      }
      return this.getCacheIcon(VIP_MEDAL[i])
    },
    // icon 字体大小
    iconSize() {
      const { recompensas_vip_placeholder_lock_icon_size } = _common
      return recompensas_vip_placeholder_lock_icon_size
    },
    vipCardBg() {
      const result = getCacheIcon('vip_swiper_bg')
      if (result) {
        const src = typeof result === 'string' ? result : result.value.src
        const { recompensas_vip_card_bg } = _common
        return {
          background: this.isApp ? `${recompensas_vip_card_bg}` : `${recompensas_vip_card_bg}, url(${src}) 100% 100%`,
          backgroundSize: '100% 100%',
          backgroundRepeat: 'no-repeat',
          backgroundBlendMode: 'lighten'
        }
      }
    }
  },
  methods: {
    getCacheIcon(k) {
      const result = getCacheIcon(k)
      return typeof result === 'string' ? result : result.value.src
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/recompensas.modules.scss';

// 针对移动端样式
.comp.vip_card {
  min-height: 100%;
  box-sizing: border-box;
  background: $recompensas_vip_card_bg;
  position: relative;
  padding: $recompensas_vip_card_app_padding;
  border-radius: $recompensas_vip_card_app_radius;
  border: $recompensas_vip_card_border;
  .vip_card_inner {
    width: $recompensas_vip_card_inner_width;
    height: 100%;
    display: flex;
    flex-direction: column;
    .vip_card_title_level {
      font-size: $recompensas_vip_card_title_level_size_app;
      font-weight: $recompensas_vip_card_title_level_bold;
      color: $recompensas_vip_card_title_level_color;
      font-family: $font-family-game-base;
    }
    .vip_card_title_level_cross {
      font-size: $recompensas_vip_card_title_level_cross_size_app;
      font-weight: $recompensas_vip_card_title_level_cross_bold;
      margin: $recompensas_vip_card_title_level_cross_margin;
    }
  }
  .vip_placeholder_lock {
    @include setBox(row, center, center);
    position: absolute;
    top: $recompensas_vip_placeholder_lock_top;
    right: $recompensas_vip_placeholder_lock_right;
    background: $recompensas_vip_placeholder_lock_bg;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    z-index: 9;
    padding: $recompensas_vip_placeholder_lock_padding;
    border-radius: $recompensas_vip_placeholder_lock_app_border;
  }
  .vip_card_medal {
    width: $recompensas_vip_card_medal_width_app;
    position: absolute;
    top: $recompensas_vip_card_medal_top_app;
    right: $recompensas_vip_card_medal_right_app;
    z-index: 8;
    &.highest {
      width: $recompensas_vip_card_medal_highest_app_width;
      right: $recompensas_vip_card_medal_highest_app_right;
    }
    img {
      width: 100%;
    }
  }
  .vip_horn {
    width: $recompensas_vip_card_vip_horn_width;
    height: $recompensas_vip_card_vip_horn_height;
    position: absolute;
    right: $recompensas_vip_card_vip_horn_right;
    top: $recompensas_vip_card_vip_horn_top;
    background: $recompensas_vip_card_vip_horn_bg;
    background-size: 100% 100%;
    background-repeat: no-repeat;
  }
}

// 针对pc样式
@include breakpoint-only-up($px: 1080px) {
  .comp.vip_card {
    padding: $recompensas_vip_card_pc_padding;
    border-radius: $recompensas_vip_card_pc_radius;
    .vip_card_inner {
      .vip_card_title_level {
        font-size: $recompensas_vip_card_title_level_size_pc;
      }
      .vip_card_title_level_cross {
        font-size: $recompensas_vip_card_title_level_cross_size_pc;
      }
    }
    .vip_placeholder_lock {
      border-radius: $recompensas_vip_placeholder_lock_pc_border;
    }
    .vip_card_medal {
      width: $recompensas_vip_card_medal_width_pc;
      top: $recompensas_vip_card_medal_top_pc;
      right: $recompensas_vip_card_medal_right_pc;
      &.highest {
        width: $recompensas_vip_card_medal_highest_pc_width;
        right: $recompensas_vip_card_medal_highest_pc_right;
      }
    }
  }
}
</style>
