<template>
  <div class="comp vip_process_wrap">
    <!-- 保级 && 当前等级 -->
    <template v-if="keepLevel && item.id == userVip">
      <div class="grading_box_recharge">
        <div class="grading_box_recharge_left">
          <div class="keep_block">
            <div class="keep_evel_text">
              <span>{{ $t('UI_VIP_Keep_Level_Text') }}</span>
            </div>
            <div v-zoom class="keep_action">
              <div class="keep_btn btn_radius" @click="onRechargeWithdrawal">
                <auto-font
                  :uuid="`vip_btn_recarega`"
                  :max-font="14"
                  :level="2"
                  :text="$t('UI_VIP_Recarega_Btn')"
                ></auto-font>
              </div>
            </div>
          </div>
          <div class="keep_progress_box">
            <div class="progress_title">
              <span class="recharge_text">{{ $t('UI.Charge_0_main_3') }}</span>
              <span>
                {{ $u.formatKilobit(item.week_recharge, isKilobit) }}/{{
                  $u.formatKilobit(item.lv_keep_recharge, isKilobit)
                }}
              </span>
            </div>
            <v-progress-linear
              class="progress_linear"
              :value="(item.week_recharge / item.lv_keep_recharge) * 100"
              :color="activeColor[0]"
              :background-color="activeColor[1]"
              rounded
            >
            </v-progress-linear>
          </div>
        </div>
        <div class="grading_box_recharge_rigth"></div>
      </div>
    </template>
    <template v-else-if="rechargeItem">
      <div class="progress_box_recharge">
        <div class="progress_box_recharge_left">
          <!-- 进度条1 -->
          <div class="progress_title">
            <span class="recharge_text">{{ $t('UI_VIP_Vip.deposit') }}</span>
            <span>
              {{ $u.formatKilobit(rechargeItem.recharge_gold, isKilobit) }}/{{
                $u.formatKilobit(rechargeItem.min_recharge, isKilobit)
              }}
            </span>
          </div>
          <v-progress-linear
            class="progress_linear"
            :value="(rechargeItem.recharge_gold / rechargeItem.min_recharge) * 100"
            :color="activeColor[0]"
            :background-color="activeColor[1]"
            rounded
          >
          </v-progress-linear>
          <!-- 进度条2 -->
          <div class="progress_box_coding">
            <div class="progress_title">
              <span class="recharge_text">{{ $t('UI_VIP_Vip.bet') }}</span>
              <span>
                {{ $u.formatKilobit(rechargeItem.vip_bets, isKilobit) }}/{{
                  $u.formatKilobit(rechargeItem.min_bet, isKilobit)
                }}
              </span>
            </div>
            <v-progress-linear
              class="progress_linear"
              :value="(rechargeItem.vip_bets / rechargeItem.min_bet) * 100"
              :color="activeColor[0]"
              :background-color="activeColor[1]"
              rounded
            >
            </v-progress-linear>
          </div>
        </div>
        <div class="progress_box_recharge_rigth"></div>
      </div>
    </template>
    <!--
        /**
        * 获取 vip 列表数据
        * 移动：（使用show_vip_list）
        *    min_bet 当前等级需要的打码量    min_recharge 当前等级需要的充值数z
        *    进度条升级对应下一等级
        *
        *   recharge_gold  当前充值数 vip_bets 当前打码
        */
                -->
  </div>
</template>

<script>
import { mapState } from 'vuex'
import _common from '@style/views/recompensas.modules.scss'

export default {
  name: 'VipProcess',
  props: {
    keepLevel: Boolean,
    item: Object,
    rechargeItem: Object,
    userVip: Number,
    // vip千分位开关
    isKilobit: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    // 进度条颜色
    activeColor() {
      const { recompensas_vip_progress_bg, recompensas_vip_progress_color } = _common
      return [recompensas_vip_progress_color, recompensas_vip_progress_bg]
    }
  },
  filters: {
    // 进度条百分百转化
    conversion(item = {}, sign = '') {
      if (!sign) return '0%'

      const { recharge_gold = 0, min_recharge = 0, vip_bets = 0, min_bet = 0 } = item
      if (sign == 'deposit') {
        const num = (recharge_gold / min_recharge) * 100
        return Math.ceil(num > 100 ? 100 : num) + '%'
      } else {
        const num = (vip_bets / min_bet) * 100
        return Math.ceil(num > 100 ? 100 : num) + '%'
      }
    }
  },
  methods: {
    // 充值提现
    onRechargeWithdrawal() {
      this.$store.dispatch('modal/setDepositWithdrawShow')
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/recompensas.modules.scss';

// 针对移动端样式
.comp.vip_process_wrap {
  // margin: 50px 0px 0px 0px;
  ::v-deep .v-progress-linear__determinate {
    border-radius: 8px !important;
  }
  .grading_box_recharge {
    @include setBox(row, space-between, center);
    .grading_box_recharge_left {
      flex-shrink: 1;
      flex-grow: 1;
      .keep_block {
        @include setBox(row, center, flex-end);
        .keep_evel_text {
          flex-grow: 1;
          flex-shrink: 1;
          font-size: $recompensas_vip_process_keep_size;
          font-weight: $recompensas_vip_process_keep_bold;
          margin: $recompensas_vip_process_keep_app_margin;
        }
        .keep_action {
          flex-grow: 0;
          flex-shrink: 0;
          z-index: 9;
          .keep_btn {
            @include setBox(row, center, center);
            width: $recompensas_vip_process_keep_btn_width;
            height: $recompensas_vip_process_keep_btn_height;
            text-align: $recompensas_vip_process_keep_btn_align;
            background: $recompensas_vip_process_keep_btn_bg;
            box-shadow: $recompensas_vip_process_keep_btn_shadow;
            &:hover {
              @include setFilter($btn_hover_filter, $btn_hover_filter_type);
            }
          }
        }
      }
      .keep_progress_box {
        padding: $recompensas_vip_process_keep_progress_box_app_padding;
        .progress_title {
          font-size: $recompensas_vip_process_keep_progress_title_size;
          font-weight: $recompensas_vip_process_keep_progress_title_bold;
          margin: $recompensas_vip_process_keep_progress_title_margin;
          @include setBox(row, space-between, center);
          .recharge_text {
            font-size: 18px;
            color: $recompensas_vip_process_keep_progress_title_color;
          }
        }
        .progress_linear {
          height: $recompensas_vip_process_keep_progress_linear_height !important;
        }
      }
    }
    .grading_box_recharge_rigth {
      flex-shrink: 0;
      flex-grow: 0;
      width: $recompensas_vip_process_grading_box_recharge_rigth_width;
      display: none;
    }
  }
  .progress_box_recharge {
    margin: $recompensas_vip_progress_box_recharge_margin_app;
    @include setBox(row, space-between, center);
    .progress_box_recharge_left {
      flex-shrink: 1;
      flex-grow: 1;
      .progress_title {
        font-size: $recompensas_vip_progress_box_recharge_title_size;
        font-weight: $recompensas_vip_progress_box_recharge_title_bold;
        margin: $recompensas_vip_progress_box_recharge_title_margin;
        @include setBox(row, space-between, center);
        .recharge_text {
          font-size: 18px;
          color: $recompensas_vip_progress_box_recharge_title_color;
        }
      }
      .progress_linear {
        height: $recompensas_vip_progress_box_recharge_linear_height !important;
      }
    }
    .progress_box_recharge_rigth {
      flex-shrink: 0;
      flex-grow: 0;
      width: $recompensas_vip_progress_progress_box_recharge_rigth_width;
      display: none;
    }
  }
  .progress_box_coding {
    margin: $recompensas_vip_progress_box_coding_margin;
    .progress_title {
      font-size: $recompensas_vip_progress_box_coding_title_size;
      font-weight: $recompensas_vip_progress_box_coding_title_bold;
      margin: $recompensas_vip_progress_box_coding_title_margin;
      @include setBox(row, space-between, center);
      .recharge_text {
        font-size: 18px;
        color: $recompensas_vip_progress_box_coding_title_color;
      }
    }
    .progress_linear {
      height: $recompensas_vip_progress_box_coding_linear_height !important;
    }
  }
}

// 针对pc样式
@include breakpoint-only-up($px: 1080px) {
  .comp.vip_process_wrap {
    .grading_box_recharge {
      .grading_box_recharge_left {
        .keep_block {
          .keep_evel_text {
            margin: $recompensas_vip_process_keep_pc_margin;
          }
        }
        .keep_progress_box {
          padding: $recompensas_vip_process_keep_progress_box_pc_padding;
        }
      }
      .grading_box_recharge_rigth {
        display: inline-block;
      }
    }
    .progress_box_recharge {
      margin: $recompensas_vip_progress_box_recharge_margin_pc;
      .progress_box_recharge_rigth {
        display: inline-block;
      }
    }
  }
}
</style>
