<template>
  <div class="comp gold_box">
    <div class="gold_box_head line_1">
      <auto-font
        :max-font="commonStyle('recompensas_gold_box_head_max_size')"
        :font-weight="commonStyle('recompensas_gold_box_head_weight')"
        :text="getTitle"
      />
    </div>
    <div class="gold_box_mid" :class="{ permit: item.status == 0 }">
      <!-- <icon-font class="reward" name="icon-icon_VIP_gift02" :size="commonStyle('recompensas_gold_box_reward_size')" /> -->
      <div class="mid_num">
        <auto-font
          :max-font="commonStyle('recompensas_gold_box_mid_num_size')"
          :font-weight="commonStyle('recompensas_gold_box_mid_num_bold')"
          :text="`${currencySymbol} ${$u.formatKilobit(item.money, isKilobit) || 0}`"
        />
      </div>
    </div>
    <div class="gold_box_bottom">
      <!-- 是当前等级 && 已领取 -- 进入倒计时倒计时 -->
      <div v-if="item.id == user.vip_id && item.status == 1" class="gold_btn tempo_text btn_radius">
        <auto-font
          :max-font="commonStyle('recompensas_gold_btn_text_size')"
          :font-weight="commonStyle('recompensas_gold_btn_text_weight')"
          :text="tempoRestante"
        />
      </div>
      <!-- 是当前等级 && (不可领 || 可领) -->
      <!-- 不是当前等级 && (不可领 || 可领 || 已领取) -->
      <template v-else>
        <!-- 不能领取 -->
        <div v-if="item.status == -1" class="gold_btn no_gold_collectable btn_radius">
          <icon-font name="icon-icon_VIP_lock" :size="commonStyle('recompensas_gold_box_lock_size')" />
        </div>
        <!-- 已领取 -->
        <div v-else-if="item.status == 1" class="gold_btn btn_radius disabled_btn">
          <auto-font
            :max-font="commonStyle('recompensas_gold_btn_text_size')"
            :font-weight="commonStyle('recompensas_gold_btn_text_weight')"
            :text="stateDesc"
          />
        </div>
        <!-- 可领取 -->
        <div v-else class="gold_btn gold_btn_active btn_radius" @click="submit">
          <auto-font
            :max-font="commonStyle('recompensas_gold_btn_text_size')"
            :font-weight="commonStyle('recompensas_gold_btn_text_weight')"
            :text="stateDesc"
          />
        </div>
      </template>
    </div>
    <i v-if="item.status == 0" class="red_radius vip_desp_dot"></i>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { receiveReward } from '@/api/modules/bet.js'
import PublishSubscribe from 'publish-subscribe-js'
import VipType from '@/events/vip'
import _common from '@style/views/recompensas.modules.scss'

export default {
  props: {
    name: String, // week 周 month 月
    // vip 列表
    vipList: {
      type: Array,
      default() {
        return []
      }
    },
    item: {
      // 当前礼包的数据
      type: Object,
      default: () => {
        return {
          status: -1, // 状态：0 可领取 -1 不能领取 1 已领取
          money: -1 // 金额
        }
      }
    },
    keepItem: {
      // 保级金额
      type: Object,
      default() {
        return {
          keep_recharge: 0, // 保级需要充值总金额
          recharge: 0 // 当前已经充值的金额
        }
      }
    },
    // vip千分位开关
    isKilobit: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    ...mapState('layout', ['containerBox']),
    ...mapState('base', ['user', 'isApp', 'languageType', 'systemConfig']),
    ...mapState('switchState', ['currencyShow']),
    // 日周月文案
    getTitle() {
      if (this.name == 'day') return this.$t('UI_VIP_Vip.DailyGift')
      if (this.name == 'week') return this.$t('UI_VIP_Vip.WeekGift')
      if (this.name == 'month') return this.$t('UI_VIP_Vip.MonthlyGift')

      return ''
    },
    // 对应显示的状态 0 可领取 1 已领取 -1 不能领取
    stateDesc() {
      const { status = -1 } = this.item
      return (
        {
          0: this.$t('UI_VIP_Vip.disponível'),
          1: this.$t('UI_VIP_Vip.recebido'),
          '-1': this.$t('UI_VIP_Vip.notAllow')
        }[status] || ''
      )
    },
    // 金额标识
    currencySymbol() {
      return this.currencyShow ? this.systemConfig.currency_symbol : ''
    },
    /**
     * 是否正在保级中
     * is_keep_level 0保级中 1未保级
     */
    isKeepLevel() {
      const { is_keep_level = 0 } = this.user
      return is_keep_level != 1
    },
    // 周，月 当前等级可领取的时候显示还剩多少时间可以领取
    tempoRestante() {
      let time = {}
      // 当前时间时间戳
      const currentTime = new Date().getTime()
      // 日礼包
      if (this.name == 'day') return `01 ${this.$t('UI_VIP_DATE_Day')} ${this.$t('UI_VIP_DATE_later')}`
      // 周
      if (this.name == 'week') {
        // 下次领取的时间戳
        let lowerTime = this.$u.datetime.getTime(-7)
        lowerTime = `${lowerTime} 00:00:00`
        time = this.$u.datetime.getDifferTime(currentTime, lowerTime)
      }
      // 月
      if (this.name == 'month') {
        // 下个月 1 号
        const newMonth = `${this.$u.datetime.newMonth()} 00:00:00`
        time = this.$u.datetime.getDifferTime(currentTime, newMonth)
      }

      const { day = 0, hh = 0, mm = 0, ss = 0 } = time
      if (Number(day))
        return `${day} ${this.$t(Number(day) > 0 ? 'UI_VIP_DATE_Days' : 'UI_VIP_DATE_Day')} ${this.$t('UI_VIP_DATE_later')}`
      if (Number(hh)) return `${hh} ${this.$t('UI_VIP_DATE_hour')} ${this.$t('UI_VIP_DATE_later')}`
      if (Number(mm)) return `${mm} ${this.$t('UI_VIP_DATE_minute')} ${this.$t('UI_VIP_DATE_later')}`
      if (Number(ss)) return `${ss} ${this.$t('UI_VIP_DATE_second')} ${this.$t('UI_VIP_DATE_later')}`

      return `0 ${this.$t('UI_VIP_DATE_Day')} ${this.$t('UI_VIP_DATE_later')}`
    }
  },
  methods: {
    commonStyle: k => _common[k],
    // 等点击领取奖励
    async submit() {
      // 请求接口获取礼包
      const { status = -1, id = 0 } = this.item
      // 对应显示的状态 0 可领取 -1 不能领取 1 已领取
      if (status != 0) return

      // 如果是保级中点击出现保级弹窗
      // if (this.isKeepLevel) {
      // 	// 获取当前等级的 数据
      // 	const currentItem = this.vipList.find((f) => f.id == this.user.vip_id) || {}
      // 	const money = (currentItem.lv_keep_recharge || 0) - (currentItem.week_recharge || 0)
      // 	const content = this.$i18n.t('UI_VIP_Recharge_Amount', {
      // 		money: this.currencySymbol + money + ''
      // 	})
      // 	return this.$pop.message().show({ content })
      // }

      const params = {
        token: this.user.token,
        type: this.name == 'day' ? 5 : this.name == 'week' ? 2 : 3,
        id: this.user.vip_id,
        language: this.languageType
      }

      const { code, msg } = await receiveReward(params).catch(e => e)
      if (code != 0) return this.$pop.message().show({ content: msg })
      const dateBonus = {
        week: this.$t('UI_Weekly_Bonus'),
        month: this.$t('UI_Monthly_Bonus')
      }

      // 领取成功后，状态变成 已领取
      PublishSubscribe.publish(VipType.RENEW_STATUS, { id, sign: this.name })

      // 插入弹窗序列
      PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_KEY, {
        key: 'deposit',
        seat: 'first',
        params: {
          title: dateBonus[this.name],
          value: this.item.money
        }
      })

      // 更新红点信息
      this.$store.dispatch('base/showRedDotReceivedFun', {
        version: 2,
        event: 'vip_cash_gift',
        language: this.$store.state.base.languageType,
        token: this.$store.state.base.user.token
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/recompensas.modules.scss';

// 针对移动端样式
.comp.gold_box {
  @include setBox(column, flex-start, center);
  width: $recompensas_gold_box_width;
  background: $recompensas_gold_box_bg;
  border: $recompensas_gold_box_border;
  border-radius: $recompensas_gold_box_app_radius;
  position: relative;
  .gold_box_head {
    width: $recompensas_gold_box_head_width;
    height: $recompensas_gold_box_head_height;
    border-bottom: $recompensas_gold_box_head_border;
  }
  .gold_box_mid {
    width: $recompensas_gold_box_mid_app_width;
    padding: $recompensas_gold_box_mid_app_padding;
    @include setBox(row, center, baseline);
    &.permit {
      .reward {
        color: $recompensas_gold_box_permit_reward_color;
      }
      .mid_num {
        color: $recompensas_gold_box_permit_mid_num_color;
      }
    }
    .reward {
      color: $recompensas_gold_box_mid_reward_color;
    }
    .mid_num {
      width: $recompensas_gold_box_mid_num_app_width;
      height: $recompensas_gold_box_mid_num_app_height;
      color: $recompensas_gold_box_mid_num_color;
    }
  }
  .gold_box_bottom {
    width: $recompensas_gold_box_bottom_width;
    font-size: $recompensas_gold_box_bottom_size;
    padding: $recompensas_gold_box_bottom_padding;
    .gold_btn {
      width: $recompensas_gold_btn_width;
      height: $recompensas_gold_btn_height;
      background: $recompensas_gold_btn_bg;
      @include setBox(row, center, center);
      &.tempo_text {
        background: $recompensas_gold_btn_tempo_text_bg;
        color: $recompensas_gold_btn_tempo_text_color;
      }
      &.no_gold_collectable {
        background: $recompensas_gold_btn_no_gold_collectable_bg;
      }
      &.gold_btn_active {
        background: $recompensas_gold_btn_bg;
        box-shadow: $recompensas_gold_btn_shadow;
        &:hover {
          @include setFilter($btn_hover_filter, $btn_hover_filter_type);
        }
      }
    }
  }
  .vip_desp_dot {
    position: absolute;
    right: $recompensas_gold_box_vip_desp_dot_top;
    top: $recompensas_gold_box_vip_desp_dot_right;
    width: $recompensas_gold_box_vip_desp_dot_width;
    height: $recompensas_gold_box_vip_desp_dot_height;
  }
}

// 针对pc样式
@include breakpoint-only-up($px: 1080px) {
  .comp.gold_box {
    border-radius: $recompensas_gold_box_pc_radius;
  }
}
</style>
