<template>
  <div class="comp grade_box" @click="handleBoxClick">
    <!-- 宝箱图 -->
    <div class="box_img">
      <img v-if="item.grade == 0" class="unclaimed_img" :src="boxImg" />
      <img v-if="item.grade == 1" class="other_img" :src="boxImg" />
      <img v-if="item.grade == -1" class="other_no_img" :src="boxImg" />
    </div>
    <!-- 领取文案 -->
    <div class="box_text">
      <div class="grade_title">
        <auto-font
          text-align="left"
          :max-font="commonStyle(isApp ? 'vip_award_box_text_title_size_app' : 'vip_award_box_text_title_size_pc')"
          :font-weight="
            commonStyle(isApp ? 'vip_award_box_text_title_weight_app' : 'vip_award_box_text_title_weight_pc')
          "
          :text="gradeTitle.levelText"
        />
      </div>
      <div class="grade_title_sign">
        <auto-font
          text-align="left"
          :max-font="
            commonStyle(isApp ? 'vip_award_box_text_title_sign_size_app' : 'vip_award_box_text_title_sign_size_pc')
          "
          :font-weight="
            commonStyle(isApp ? 'vip_award_box_text_title_sign_weight_app' : 'vip_award_box_text_title_sign_weight_pc')
          "
          :text="gradeTitle.text ? gradeTitle.text.toLocaleUpperCase() : ''"
        />
      </div>
      <div class="grade_title_money">
        <auto-font
          text-align="left"
          :max-font="
            commonStyle(isApp ? 'vip_award_box_text_title_money_size_app' : 'vip_award_box_text_title_money_size_pc')
          "
          :font-weight="
            commonStyle(
              isApp ? 'vip_award_box_text_title_money_weight_app' : 'vip_award_box_text_title_money_weight_pc'
            )
          "
          :text="`${currencySymbol} ${$u.formatKilobit(item.grade_gold, isKilobit)}`"
        />
      </div>
    </div>
    <!-- 可以领取,红点 -->
    <i v-if="item.grade == 0" class="red_radius vip_desp_dot"></i>
    <!-- 不能领取加锁 -->
    <div v-if="item.grade == -1" class="vip_placeholder_lock">
      <icon-font
        name="icon-icon_VIP_lock"
        :size="commonStyle('vip_award_box_vip_placeholder_lock_size')"
        :dark="commonStyle('vip_award_box_vip_placeholder_lock_color')"
      />
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import { getCacheIcon } from '@/assets/imgCache'
import { receiveReward } from '@/api/modules/bet.js'
import PublishSubscribe from 'publish-subscribe-js'
import VipType from '@/events/vip'
import _common from '@style/views/recompensas.modules.scss'

const BOX_IMG_MAP = {
  0: 'vip_ic_box3',
  1: 'vip_ic_box4',
  '-1': 'vip_ic_box2'
}

export default {
  name: 'VipAwardBox',
  props: {
    currencySymbol: String,
    // vip 列表
    vipList: {
      type: Array,
      default() {
        return []
      }
    },
    item: {
      // 当前礼包的数据
      type: Object,
      default: () => {
        return {
          grade: -1
        }
      }
    },
    // vip千分位开关
    isKilobit: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {}
  },
  computed: {
    ...mapState('layout', ['containerBox']),
    ...mapState('base', ['isApp', 'user']),
    ...mapGetters('base', ['getVipCurrentLevel']),
    /**
     * 是否正在降级
     * is_keep_level 0降级 1未降级
     */
    isKeepLevel() {
      const { is_keep_level = 0 } = this.user
      return is_keep_level != 1
    },
    // 宝箱图片
    boxImg() {
      const { grade } = this.item
      return this.getCacheIcon(BOX_IMG_MAP[grade])
    },
    // 宝箱领取文案
    gradeTitle() {
      return {
        levelText: this.$t('UI_VIP_Share.grade') + this.getVipCurrentLevel(this.item.id),
        text: this.$t('UI_VIP_Vip.vipTitles')
      }
    }
  },
  methods: {
    commonStyle: k => _common[k],
    getCacheIcon(k) {
      const result = getCacheIcon(k)
      return typeof result === 'string' ? result : result.value.src
    },
    // 领取宝箱
    async handleBoxClick() {
      if (this.item.grade != 0) return

      // 如果是保级中点击出现保级弹窗
      // if (this.isKeepLevel) {
      // 	// 获取当前等级的 数据
      // 	const currentItem = this.vipList.find((f) => f.id == this.user.vip_id) || {}
      // 	const money = (currentItem.lv_keep_recharge || 0) - (currentItem.week_recharge || 0)
      // 	const content = this.$i18n.t('UI_VIP_Recharge_Amount', {
      // 		money: this.currencySymbol + money + ''
      // 	})
      // 	return this.$pop.message().show({ content })
      // }

      // 请求接口获取宝箱金额
      const { id = 0 } = this.item
      const params = {
        token: this.user.token,
        id,
        type: 1
      }

      const { code, msg } = await receiveReward(params).catch(e => e)
      if (code != 0) return this.$pop.message().show({ content: msg, timeout: 5000 })

      // 领取成功后，状态变成 已领取
      PublishSubscribe.publish(VipType.RENEW_STATUS, { id, sign: 'box' })

      // 插入弹窗序列
      PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_KEY, {
        key: 'deposit',
        seat: 'first',
        params: {
          title: this.$t('UI_Code_Bonus'),
          value: this.item.grade_gold
        }
      })

      // 更新红点提示
      this.$store.dispatch('base/showRedDotReceivedFun', {
        version: 2,
        event: 'vip_cash_gift',
        language: this.$store.state.base.languageType,
        token: this.$store.state.base.user.token
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/recompensas.modules.scss';

// 针对移动端样式
.comp.grade_box {
  @include setBox(row, flex-start, center);
  background: $vip_award_box_bg;
  padding: $vip_award_box_padding_app;
  border: $vip_award_box_border;
  border-radius: $vip_award_box_radius_app;
  position: relative;
  .box_img {
    width: $vip_award_box_box_img_width_app;
    padding: $vip_award_box_box_img_padding_app;
    @include setBox(row, center, center);
    border-right: $vip_award_box_box_img_border_r_app;
    .unclaimed_img {
      width: $vip_award_box_box_img_inclaomed_width_app;
    }
    .other_img {
      width: $vip_award_box_box_img_other_width_app;
    }
    .other_no_img {
      width: $vip_award_box_box_img_other_no_width_app;
    }
  }
  .box_text {
    width: $vip_award_box_text_width_app;
    margin: $vip_award_box_text_margin_app;
    @include setBox(column, center, flex-start);
    gap: $vip_award_box_text_margin_gap_app;
    .grade_title {
      width: $vip_award_box_text_title_app_width;
      height: $vip_award_box_text_title_app_height;
      color: $vip_award_box_text_title_color_app;
    }
    .grade_title_sign {
      width: $vip_award_box_text_title_sign_app_width;
      height: $vip_award_box_text_title_sign_app_height;
      color: $vip_award_box_text_title_sign_color_app;
      padding: $vip_award_box_text_title_sign_padding_app;
    }
    .grade_title_money {
      width: $vip_award_box_text_title_money_app_width;
      height: $vip_award_box_text_title_money_app_height;
      color: $vip_award_box_text_title_money_color_app;
      @include setBox(row, center, center);
    }
  }
  .vip_desp_dot {
    position: absolute;
    right: $vip_award_box_vip_desp_dot_right_app;
    top: $vip_award_box_vip_desp_dot_top_app;
    width: $vip_award_box_vip_desp_dot_width_app;
    height: $vip_award_box_vip_desp_dot_height_app;
  }
  .vip_placeholder_lock {
    @include setBox(row, center, center);
    position: absolute;
    top: $vip_award_box_vip_placeholder_lock_top_app;
    right: $vip_award_box_vip_placeholder_lock_right_app;
    background: $vip_award_box_vip_placeholder_lock_bg_app;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    z-index: 9;
    padding: $vip_award_box_vip_placeholder_lock_padding_app;
    border-radius: $vip_award_box_vip_placeholder_lock_radius_app;
  }
}

// 针对pc样式
@include breakpoint-only-up($px: 1080px) {
  .comp.grade_box {
    padding: $vip_award_box_padding_pc;
    border-radius: $vip_award_box_radius_pc;
    .box_img {
      width: $vip_award_box_box_img_width_pc;
      padding: $vip_award_box_box_img_padding_pc;
      @include setBox(row, center, center);
      border-right: $vip_award_box_box_img_border_r_pc;
      .unclaimed_img {
        width: $vip_award_box_box_img_inclaomed_width_pc;
      }
      .other_img {
        width: $vip_award_box_box_img_other_width_pc;
      }
      .other_no_img {
        width: $vip_award_box_box_img_other_no_width_pc;
      }
    }
    .box_text {
      margin: $vip_award_box_text_margin_pc;
      @include setBox(column, center, flex-start);
      gap: $vip_award_box_text_margin_gap_pc;
      .grade_title {
        color: $vip_award_box_text_title_color_pc;
      }
      .grade_title_sign {
        color: $vip_award_box_text_title_sign_color_pc;
        padding: $vip_award_box_text_title_sign_padding_pc;
      }
      .grade_title_money {
        @include setBox(row, center, center);
        color: $vip_award_box_text_title_money_color_pc;
      }
    }
    .vip_desp_dot {
      position: absolute;
      right: $vip_award_box_vip_desp_dot_right_pc;
      top: $vip_award_box_vip_desp_dot_top_pc;
      width: $vip_award_box_vip_desp_dot_width_pc;
      height: $vip_award_box_vip_desp_dot_height_pc;
    }
    .vip_placeholder_lock {
      @include setBox(row, center, center);
      position: absolute;
      top: $vip_award_box_vip_placeholder_lock_top_pc;
      right: $vip_award_box_vip_placeholder_lock_right_pc;
      background: $vip_award_box_vip_placeholder_lock_bg_pc;
      background-size: 100% 100%;
      background-repeat: no-repeat;
      z-index: 9;
      padding: $vip_award_box_vip_placeholder_lock_padding_pc;
      border-radius: $vip_award_box_vip_placeholder_lock_radius_pc;
    }
  }
}
</style>
