<template>
  <v-container ref="boxIds" class="recompensas_container c_box">
    <div class="page recompensas">
      <space-ui sign="recompensas" v-if="skeletonStatus"></space-ui>
      <template v-else>
        <div class="recompensas_card">
          <swiper ref="swiperRef" :options="swiperOptions">
            <swiper-slide v-for="(item, index) in vipList" :key="item.vip_id" :style="slideStyle">
              <vip-card
                :item="item"
                :highest="vipList.length - 1 == index"
                :total-grade="vipList.length"
                :is-active="vipIndex === index"
                :index="index"
                :vip-list="vipList"
                :is-kilobit="isKilobit"
              >
              </vip-card>
            </swiper-slide>
          </swiper>
        </div>
        <div class="swiper_btn">
          <div class="swiper-button-prev" :class="{ aniprev: animStatus == 1 }">
            <icon-font name="icon-arrow_left" :size="iconSize.size" :dark="iconSize.color" />
          </div>
          <div class="swiper-button-next" :class="{ aninext: animStatus == 2 }">
            <icon-font name="icon-arrow_right" :size="iconSize.size" :dark="iconSize.color" />
          </div>
        </div>
        <div class="recompensas_box">
          <swiper ref="swiperCardRef" :options="swiperOptionsInfo">
            <swiper-slide v-for="(item, index) in vipList" :key="item.vip_id">
              <vip-award
                :item="item"
                :info-desc="infoDesc"
                :info-img="infoImg"
                :highest="vipList.length - 1 == index"
                :vip-list="vipList"
                :is-kilobit="isKilobit"
              >
              </vip-award>
            </swiper-slide>
          </swiper>
        </div>
      </template>
    </div>
  </v-container>
</template>

<script>
import { mapState } from 'vuex'
import PublishSubscribe from 'publish-subscribe-js'
import VipType from '@/events/vip'
import ContainerMixin from '@/components/containerMixin'
import VipCard from './ui/vip-card'
import VipAward from './ui/vip-award'
import spaceUi from '@/components/skeleton/space-ui.vue'
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import { vipList as getInterfaceVipList } from '@/api/modules/bet.js'
import { getInfoCache } from '@/api/modules/config.js'
import _common from '@style/views/recompensas.modules.scss'
import 'swiper/css/swiper.css'

export default {
  name: 'Recompensas',
  mixins: [ContainerMixin],
  components: {
    spaceUi,
    Swiper,
    SwiperSlide,
    VipCard,
    VipAward
  },
  data() {
    return {
      vipIndex: 0, // 当前vip的索引
      skeletonStatus: true, // 骨架屏显隐
      timer: null, // 手动loading
      swiperOptions: {},
      swiperOptionsInfo: {},
      swiperCardHeight: 0, // swiper 的最高高度
      animIndex: null, // 用于判断滑动时前进/后退按钮触发动画
      animStatus: null, // 2前进 1后退

      vipList: [], // vip 等级数据【vip 合并的列表数据】
      infoDesc: '', // VIP保级降级功能说明
      infoImg: '', // VIP保级降级功能说明配置的图片
      badgeLt: false, // 左边滑动的徽标红点
      badgeRt: false, // 右边边滑动的徽标红点
      isKilobit: false // vip千分位开关
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType']),
    // 当前的vip等级
    vipLevel() {
      return this.user.vip_id - 1
    },
    // 头部卡片宽度
    slideStyle() {
      const { recompensas_card_app_width, recompensas_card_pc_width } = _common
      return {
        width: this.isApp ? recompensas_card_app_width : recompensas_card_pc_width,
        height: this.swiperCardHeight ? `${this.swiperCardHeight}px` : 'auto'
      }
    },
    // 切换按钮大小
    iconSize() {
      const { recompensas_swiper_btn_size, recompensas_swiper_btn_bg } = _common
      return {
        size: recompensas_swiper_btn_size,
        color: recompensas_swiper_btn_bg
      }
    }
  },
  watch: {
    // 监听国际化切换
    languageType(n) {
      this.getVipList()
    },
    vipIndex(val) {
      // this.getIsGift(val);
      // this.vipIndex = index
    },
    isApp(val) {
      // this.$nextTick(() => {
      // 	this.$forceUpdate();
      // })
      // this.initSwiperOption()
      // console.log(this.$refs.swiperCardRef,"swiperCardRef")
    }
  },
  created() {
    this.getVipList()
    this.initSwiperOption()
  },
  mounted() {
    // 发布订阅, 更新vip列表
    this.renewList = () => this.getVipList()
    PublishSubscribe.subscribe(VipType.RENEW_LIST, this.renewList)
    // 发布订阅, 更新vip状态
    this.renewStatus = obj => this.updateBonus(obj)
    PublishSubscribe.subscribe(VipType.RENEW_STATUS, this.renewStatus)
  },
  methods: {
    /**
     * 获取 vip 列表数据
     * pc: （直接使用最外层）
     *   max_bets 最大打码 max_recharge 最大充值数
     * 移动：（使用show_vip_list）
     *    min_bet 打码    min_recharge充值
     *    进度条升级对应下一等级
     *
     *   recharge_gold  当前充值数 vip_bets 当前打码
     * 	 thousand_mark vip千分位开关 0关 1开
     */
    async getVipList() {
      let state = true
      const par = {
        token: this.user.token,
        language: this.languageType
      }
      this.timer = setTimeout(() => {
        if (state) this.$pop.loading().show()
      }, 2000)
      const res = await getInterfaceVipList(par).catch(e => e)
      state = false
      if (this.timer) clearTimeout(this.timer) // 清除
      this.$pop.loading().hide()

      const { code, data } = res
      if (code != 0) return

      // vip_bets          pc端 当前打码量
      // max_recharge      pc端 最大充值数
      // recharge_gold     pc端 当前充值数
      // vip_id            当前等级对应的列表 id
      // vip_faq_name      根据这个字段获取VIP保级降级功能说明
      // lv_keep_recharge  开启保级后，当前等级要充值多少钱才能解开保级 ------- 保级使用
      // week_recharge     本周已经充值了多少钱 ------------------------------ 保级使用
      const list = data.list && Array.isArray(data.list) ? data.list : []
      const vipListOld = data.show_vip_list && Array.isArray(data.show_vip_list) ? data.show_vip_list : []
      // 合并两个列表的数据
      const vipListNew = vipListOld.map(item => {
        const vipItem = list.find(f => f.id == item.id)
        // 判断shouVipList 里面没有这个几个字段删除list
        if (!item.withdraw_fee_rate) {
          delete vipItem.withdraw_fee_rate
        }
        if (!item.withdraw_num) {
          delete vipItem.withdraw_num
        }
        if (!item.withdraw_each_limit) {
          delete vipItem.withdraw_each_limit
        }
        if (!item.withdraw_day_limit) {
          delete vipItem.withdraw_day_limit
        }
        if (!item.usdt_withdraw_fee_rate) {
          delete vipItem.usdt_withdraw_fee_rate
        }
        if (!item.usdt_withdraw_num) {
          delete vipItem.usdt_withdraw_num
        }
        if (!item.usdt_withdraw_each_limit) {
          delete vipItem.usdt_withdraw_each_limit
        }
        if (!item.usdt_withdraw_day_limit) {
          delete vipItem.usdt_withdraw_day_limit
        }

        const newItem = { ...vipItem, ...item }
        return {
          ...newItem,
          recharge_gold: data.recharge_gold, // 当前充值数
          vip_bets: data.vip_bets, // 当前打码
          week_recharge: data.week_recharge // 当前打码
        }
      })

      this.vipList = vipListNew
      await this.getGrauInfo(data.vip_faq_name)

      // thousand_mark vip千分位开关 0关 1开
      this.isKilobit = data.thousand_mark == 1

      // 新版停用
      // this.getIsGift(data.vip_id - 1);

      this.skeletonStatus = false
    },
    // 获取 vip 保级降级功能说明
    async getGrauInfo(name = '') {
      if (!name) return

      const params = {
        token: this.user.token,
        language: this.languageType,
        name,
        type: 3
      }

      const { code, data = {} } = await getInfoCache(params).catch(e => e)
      if (code != 0) return

      const info = data.value || ''
      // info = info.replace(eval(/<br\/>/g), '');
      const img = data.img_url || ''

      this.infoDesc = info
      this.infoImg = img
    },
    /**
     * 更新日礼，月礼，周礼，宝箱奖励状态
     * @param {*} obj { id: 对应的id, sign: 标识 } day 日, week: 周, month 月, box 宝箱
     * 0 可领取 -1 不能领取 1 已领取
     */
    updateBonus(obj = {}) {
      const { id = 0, sign = 'week' } = obj

      let vipListNew = JSON.parse(JSON.stringify(this.vipList))
      vipListNew = vipListNew.map(item => {
        if (item.id == id) {
          switch (sign) {
            case 'day':
              const { vip_id = 1 } = this.user
              item.day = vip_id == id ? 1 : -1
              break
            case 'week':
              item.week = 1
              break
            case 'month':
              item.month = 1
              break
            case 'box':
              item.grade = 1
              break

            default:
              break
          }

          return item
        }
        return item
      })

      this.vipList = vipListNew
    },
    /**
     * 判断当前显示的等级页面，前后有没有可领取的礼物
     * @param {*} level 当前的等级
     */
    getIsGift(level = 0) {
      // 所有最高等级
      const vip_l = this.vipList.length - 1
      // 当前显示的等级页面左边上一级的等级
      let leftLevel = level - 1
      leftLevel = leftLevel >= 0 ? leftLevel : vip_l
      // 截取左边上一级之前的数组，比如上一级是 5 级 就截取 0-5级的数组
      const leftArr = this.vipList.slice(0, leftLevel + 1)
      // 0 可领取 -1 不能领取 1 已领取
      const isLeft = leftArr.map(left => left.week == 0 || left.month == 0 || left.grade == 0)
      this.badgeLt = isLeft.includes(true)

      // 当前显示的等级页面右边下一级的等级
      let rightLevel = level + 1
      rightLevel = rightLevel > vip_l ? 0 : rightLevel
      // 截取右边下一级之后的数组，比如下一级是 6 级 就截取 6-vip_l 级的数组
      const rightArr = this.vipList.slice(rightLevel, vip_l + 1)
      // 0 可领取 -1 不能领取 1 已领取
      const isRight = rightArr.map(right => right.week == 0 || right.month == 0 || right.grade == 0)
      this.badgeRt = isRight.includes(true)
    },
    // swiper 初始化
    initSwiperOption() {
      const _this = this
      const initialSlide = this.user.vip_id - 1
      this.swiperOptions = {
        initialSlide,
        slidesPerView: 'auto',
        centeredSlides: true,
        loop: false,
        autoplay: false,
        followFinger: false,
        resistanceRatio: 0.1,
        spaceBetween: this.isApp ? 8 : 16,
        on: {
          init: () => {
            this.$nextTick(() => {
              const { swiperRef } = this.$refs
              if (!swiperRef) return

              this.swiperCardHeight = swiperRef?.$swiper?.height
            })
          },
          slideNextTransitionEnd: () => {
            this.vipIndex++
          },
          slidePrevTransitionEnd: () => {
            this.vipIndex--
          },
          slideChangeTransitionStart: function () {
            if (_this.animIndex == null) return (_this.animIndex = this.activeIndex)

            // 前进
            if (_this.animIndex < this.activeIndex) {
              _this.animIndex = this.activeIndex
              _this.animStatus = 2
            }
            // 后退
            if (_this.animIndex > this.activeIndex) {
              _this.animIndex = this.activeIndex
              _this.animStatus = 1
            }
            setTimeout(() => {
              _this.animStatus = null
            }, 1 * 150)
          }
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        },
        noSwipingSelector: '.keep_btn' // 禁止元素拖动
      }
      this.swiperOptionsInfo = {
        initialSlide,
        slidesPerView: 1,
        centeredSlides: true,
        loop: false,
        autoplay: false,
        allowTouchMove: false,
        on: {
          init: () => {
            this.$nextTick(() => {
              const { swiperRef, swiperCardRef } = this.$refs
              swiperRef.$swiper.controller.control = swiperCardRef.$swiper
            })
          }
        }
      }
    }
  },
  beforeDestroy() {
    // 卸载订阅
    PublishSubscribe.unsubscribe(VipType.RENEW_LIST)
    PublishSubscribe.unsubscribe(VipType.RENEW_STATUS)
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/recompensas.modules.scss';

.recompensas_container {
  padding: $recompensas_container_padding !important;
}

// 针对移动端样式
.page.recompensas {
  width: $page_recompensas_media_app_width;
  user-select: none;
  cursor: pointer;
  .recompensas_card {
    padding: $recompensas_card_padding;
  }
  .swiper_btn {
    padding: $recompensas_swiper_btn_padding;
    @include setBox(row, center, center);
    .swiper-button-prev,
    .swiper-button-next {
      width: calc(#{$recompensas_swiper_btn_size}px);
      height: calc(#{$recompensas_swiper_btn_size}px);
      margin: $recompensas_swiper_btn_margin;
      font-size: 0;
      position: initial !important;
      &::after {
        display: none;
      }
      &.aniprev,
      &.aninext {
        animation-name: btn-anim;
        animation-duration: 1s;
        animation-timing-function: linear;
        animation-iteration-count: 0.15;
        animation-fill-mode: both;
        @keyframes btn-anim {
          0% {
            transform: scale(1);
          }
          50% {
            transform: scale(2);
          }
          100% {
            transform: scale(1);
          }
        }
      }
    }
    .swiper-button-prev {
      margin: 0;
    }
  }
  .recompensas_box {
    padding: $recompensas_box_padding;
  }
}

// 针对pc样式
@include breakpoint-only-up($px: 1080px) {
  .page.recompensas {
    width: $page_recompensas_media_pc_width;
  }
}
</style>
