<template>
  <div class="comp vip_slide_wrap">
    <div class="vip_award_view">
      <div class="vip_award_view_left">
        <vip-award-box :currency-symbol="currencySymbol" :item="item" :vip-list="vipList" :is-kilobit="isKilobit" />
      </div>
      <!-- 周礼，月礼 -->
      <div v-if="weeklyRitual.length > 0" class="weekly_month">
        <div class="cash" :class="{ single: weeklyRitual.length == 1 }" v-for="(gift, i) in weeklyRitual" :key="i">
          <gold-box
            :vip-list="vipList"
            :item="gift"
            :name="gift.sign"
            :keep-item="{
              keep_recharge: item.lv_keep_recharge,
              recharge: item.week_recharge
            }"
            :is-kilobit="isKilobit"
          >
          </gold-box>
        </div>
      </div>
    </div>
    <v-sheet color="t_card_bg" class="award_privilege">
      <icon-font
        v-if="!isApp"
        class="award_privilege_icon_left"
        name="icon-icon_email_chip"
        :size="variables.vip_award_slide_award_privilege_icon_left_size"
      >
      </icon-font>
      <icon-font
        v-if="!isApp"
        class="award_privilege_icon_rigth"
        name="icon-icon_email_chip"
        :size="variables.vip_award_slide_award_privilege_icon_rigth_size"
      >
      </icon-font>
      <div class="crown_icon">
        <img :src="getCacheIcon('vip_crown')" alt="" />
      </div>
      <span class="c_box cor_head">{{ $t('UI_VIP_Vip.privilegio') }}</span>
      <div class="cor_content c_box" v-if="item.withdraw_fee_rate">
        <span class="text">{{ $t('UI_VIP_Vip.tomarText') }}&nbsp;</span>
        <span>{{ item.withdraw_fee_rate }}</span>
      </div>
      <div class="cor_content c_box" v-if="item.withdraw_num">
        <span class="text">{{ $t('UI_VIP_Vip.desenhoText') }}&nbsp;</span>
        <span>{{ $u.formatKilobit(item.withdraw_num, isKilobit) }}</span>
      </div>
      <div class="cor_content c_box" v-if="item.withdraw_each_limit">
        <span class="text">{{ $t('UI_VIP_Vip.quotaText') }}&nbsp;</span>
        <span>{{ $u.formatKilobit(item.withdraw_each_limit, isKilobit) }}</span>
      </div>
      <div class="cor_content c_box" v-if="item.withdraw_day_limit">
        <span class="text">{{ $t('UI_VIP_Vip.quotasText') }}&nbsp;</span>
        <span>{{ $u.formatKilobit(item.withdraw_day_limit, isKilobit) }}</span>
      </div>
      <div class="cor_content c_box" v-if="item.usdt_withdraw_fee_rate">
        <span class="text">{{ $t('UI_Vip_USDT_Fee') }}&nbsp;</span>
        <span>{{ item.usdt_withdraw_fee_rate }}</span>
      </div>
      <div class="cor_content c_box" v-if="item.usdt_withdraw_num">
        <span class="text">{{ $t('UI_Vip_USDT_Daily') }}&nbsp;</span>
        <span>{{ $u.formatKilobit(item.usdt_withdraw_num, isKilobit) }}</span>
      </div>
      <div class="cor_content c_box" v-if="item.usdt_withdraw_each_limit">
        <span class="text">{{ $t('UI_Vip_USDT_Linit ') }}&nbsp;</span>
        <span>{{ $u.formatKilobit(item.usdt_withdraw_each_limit, isKilobit) }}</span>
      </div>
      <div class="cor_content c_box" v-if="item.usdt_withdraw_day_limit">
        <span class="text">{{ $t('UI_Vip_USDT_Limit') }}&nbsp;</span>
        <span>{{ $u.formatKilobit(item.usdt_withdraw_day_limit, isKilobit) }}</span>
      </div>
      <!-- <div class="cor_content c_box" v-if="item.internal_percent">
        <span class="text">{{ $t('UI_VIP_Vip.bonusText') }}&nbsp;</span>
        <span>{{ item.internal_percent }}</span>
      </div> -->
    </v-sheet>

    <v-sheet color="t_card_bg" class="award_grade">
      <div class="award_grade_box" :style="awardGradeBoxFn">
        <span class="grade_head c_box">{{ $t('UI_VIP_Vip.protection') }}</span>
        <div class="grade_content c_box">
          <span class="text">{{ $t('UI_VIP_Vip.wallet') }}&nbsp;</span>
          <span>{{ currencySymbol }}{{ item.lv_keep_recharge || 0 }}/{{ $t('UI_VIP_Vip.empresa') }} </span>
        </div>
        <div class="grade_img c_box">
          <img :src="getCacheIcon('vip_money')" alt="" />
        </div>
        <div v-if="infoImg && isApp" class="grade_info_img c_box">
          <load-img height="100%" :width="getGoldBox" :item="{ url: infoImg }" />
        </div>
      </div>
      <div class="info_desc">
        <div class="info_desc_text" v-html="infoDesc"></div>
        <div v-if="infoImg && !isApp" class="grade_info_img c_box">
          <load-img height="100%" :width="getGoldBox" :item="{ url: infoImg }" />
        </div>
        <icon-font
          v-if="!isApp"
          class="award_grade_icon_left"
          name="icon-icon_email_chip"
          :size="variables.vip_award_slide_grade_info_desc_award_grade_icon_left_size"
        >
        </icon-font>
        <icon-font
          v-if="!isApp"
          class="award_grade_icon_rigth"
          name="icon-icon_email_chip"
          :size="variables.vip_award_slide_grade_info_desc_award_grade_icon_rigth_size"
        >
        </icon-font>
      </div>
    </v-sheet>

    <!-- <v-sheet color="t_card_bg" class="award_share">
			<div class="award_footer">
				<div class="award_footer_text" :class="isApp ? 'text_left' : 'text_center'">
					{{ $t("UI_VIP_Vip.tg") }}
				</div>
				<v-btn class="award_footer_btn btn_radius" color="t_btn_host_color" @click="joinAction">
					{{ $t("UI_VIP_Vip.join") }}
				</v-btn>
			</div>
		</v-sheet> -->
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getKefuListByText } from '@/api/modules/kefu.js'
import VipAwardBox from './vip-award-box'
import GoldBox from './gold-box'
import loadImg from '@/components/ui/loadImg'
import { getCacheIcon } from '@/assets/imgCache.js'
import variables from '@style/views/recompensas.modules.scss'

export default {
  name: 'VipAward',
  components: {
    VipAwardBox,
    GoldBox,
    loadImg
  },
  props: {
    // vip 列表
    vipList: {
      type: Array,
      default() {
        return []
      }
    },
    item: {
      // 单个 vip 数据
      type: Object,
      default: () => {}
    },
    infoDesc: {
      // VIP保级降级功能说明
      type: String,
      default: ''
    },
    infoImg: {
      // VIP保级降级功能说明配置的图片
      type: String,
      default: ''
    },
    highest: {
      // 是否是最高等级
      type: Boolean,
      default: false
    },
    // vip千分位开关
    isKilobit: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      variables
    }
  },
  computed: {
    ...mapState('layout', ['containerBox']),
    ...mapState('base', ['isApp', 'user', 'languageType', 'systemConfig']),
    ...mapState('switchState', ['currencyShow']),
    // 返回信息图片的宽
    getGoldBox() {
      const w = this.containerBox.width * 0.5
      return this.isApp ? (w > 160 ? 160 : w) : 213
    },
    // 金额标识
    currencySymbol() {
      return this.currencyShow ? this.systemConfig.currency_symbol : ''
    },
    /** 当前等级 */
    isCurrentLevel() {
      return this.item.id == this.user.vip_id
    },
    // 周礼，月礼, 日 处理成数组
    weeklyRitual() {
      const {
        vip_gift_gold_day_switch = 0,
        vip_gift_gold_week_switch = 0,
        vip_gift_gold_month_switch = 0
      } = this.systemConfig
      // [week, month, day] 0 可领取 1 已领取 -1 不能领取
      const { day = -1, day_gold = 0, week = -1, week_gold = 0, month = -1, month_gold = 0 } = this.item
      const dayStatus = this.isCurrentLevel ? (day == 0 ? day : 1) : day
      const weekStatus = this.isCurrentLevel ? (week == 0 ? week : 1) : week
      const monthStatus = this.isCurrentLevel ? (month == 0 ? month : 1) : month

      return [
        {
          sign: 'day',
          ...this.item,
          money: day_gold,
          status: dayStatus,
          hidden: vip_gift_gold_day_switch == 1
        },
        {
          sign: 'week',
          ...this.item,
          money: week_gold,
          status: weekStatus,
          hidden: vip_gift_gold_week_switch == 1
        },
        {
          sign: 'month',
          ...this.item,
          money: month_gold,
          status: monthStatus,
          hidden: vip_gift_gold_month_switch == 1
        }
      ].filter(f => f.hidden)
    },
    awardGradeBoxFn() {
      const result = getCacheIcon('vip_award_grade')
      if (result) {
        const src = typeof result === 'string' ? result : result.value.src
        const { vip_award_slide_award_grade_box_bg_pc } = this.variables
        return {
          background: this.isApp ? `none` : `${vip_award_slide_award_grade_box_bg_pc}, url(${src})  no-repeat fixed`,
          backgroundSize: '100%',
          backgroundBlendMode: 'lighten'
        }
      }
    }
  },
  methods: {
    getCacheIcon(k) {
      const result = getCacheIcon(k)
      return typeof result === 'string' ? result : result.value.src
    },
    // 分享
    async joinAction() {
      const params = { token: this.user.token, language: this.languageType }
      const res = await getKefuListByText(params).catch(e => e)
      if (res.code != 0) return

      const resData = res.data && Array.isArray(res.data) ? res.data : []
      const blank = resData.find(f => f.lang_name == 'group_chat_tel') || {}

      const { value = '' } = blank
      const url = value.includes('http://') || value.includes('https://') ? value : `http://${value}`

      // 兼容 移动端safari浏览器无法通过window.open在新标签打开链接
      setTimeout(() => window.open(url, '_blank'))
    }
  }
}
</script>

