      }
      .grade_content {
        font-size: $vip_award_slide_grade_content_size_app;
        font-weight: $vip_award_slide_grade_content_bold;
        margin: $vip_award_slide_grade_content_margin;
        .text {
          color: $vip_award_slide_grade_content_text_color;
        }
      }
      .grade_img {
